.content {
    display: grid;
    color: white;
    font-size: 30px;
    grid-template-areas: "avatar info"
                        ". posts";
    grid-template-columns: 240px auto;
    row-gap: 20px;
}
.avatar img{
    height: 200px;
    margin-bottom: 10px;
}
.avatar{
    padding: 10px;
    grid-area: avatar;
}
.button{
    width: 200px;
    height: 50px;
    outline: none;
    border: none;
    font-size: 20px;
    color: #646464;
    background-color: #1d1d1d;
}
.button:hover{
    background-color: rgba(255,255,255,0.5);
}
.preloader{
    text-align: center;
    padding-top: 150px;
    height: 300px;
}
